body, p, h2 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: flex;
}

.nav {
  color: #060b35;
  line-height: 60px;
}

.nav__list {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  height: 60px;
  transition: all .3s linear;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.catalog {
  --columns-count: 1;
  grid-template-columns: repeat(var(--columns-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px 75px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --columns-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --columns-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --columns-count: 4;
  }
}

.card {
  color: #000;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s linear;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__text--secondary {
  color: #616070;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 24px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 16px;
  display: flex;
}

.card__link {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s linear;
  display: flex;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--0 :nth-child(-n), .stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
